.image-cropper {
    width: 300px;
    height: 300px;
    position: relative;
    overflow: hidden;
    border-radius: 50%;
}
.bio-center a{
    /* color:brown !important; */
    text-decoration: underline;
    /* font-weight: 600 !important; */
}
.img {
    filter: grayscale(100%);
    width:100%;
    padding-top: 20px;
    padding-bottom: 20px;
}
.img:hover {
    filter: grayscale(0%);
}
.bio-container{
