}
.bio-container-main-title{
    text-align: center;
}

.bio-bg{
    background-color: white;
    padding-bottom: 60px;
}
.bio-bg p {
    color: black;
}
.bio-bg a {
    color: black;
    text-decoration: underline;
}

.bio-bg-dark{
    padding-bottom: 20px;
}



.bio-center {
    max-width: 60%;
    margin: 0 auto;
    /* padding-bottom: 80px; */
}

.text-center {
    font-size: max(62px, calc(9vw - 120px));
    padding-top: 80px;
    text-align: center;
}
.text-center-paragraph {
    font-size: x-large;
    padding-top: 20px;
    text-align: center;
}


@media (max-width: 768px) {
    .bio-center {
        max-width: 90%;
        margin-right: 0px;
        margin: 0 auto;
    }
